.Input_Group {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

/* input Î≥∏Ï≤¥ */
.Input_Group input {
  width: 100%;
  height: 46px;
  padding: 12px 12px 12px 40px;
  border: 1.5px solid #d5d8e0;
  border-radius: 10px;
  font-size: 15px;
  background: #fafbff;
  transition: all 0.25s ease;
  outline: none;
  box-sizing: border-box;
}

/* Ìè¨Ïª§Ïä§ Ïãú Í∞ïÏ°∞ */
.Input_Group input:focus {
  border-color: #4a90e2;
  background: #ffffff;
  box-shadow: 0 0 6px rgba(74, 144, 226, 0.25);
}

/* Icon */
.Input_Group i {
  position: absolute;
  top: 45%;
  left: 12px;
  transform: translateY(-50%);
  font-size: 20px;
  color: #9aa3b2;
  pointer-events: none;
  transition: color 0.25s ease;
}

.Input_Group input:focus ~ i {
  color: #4a90e2;
}

/* Floating Label */
.Input_Group label {
  position: absolute;
  left: 40px;
  top: 46%;
  transform: translateY(-50%);
  color: #9da3ae;
  font-size: 20px;
  padding: 0 4px;
  background: #fafbff;
  transition: all 0.25s ease;
  pointer-events: none;
}

/* ÌîåÎ°úÌåÖ Ï°∞Í±¥ */
.Input_Group input:focus + label,
.Input_Group input:not(:placeholder-shown) + label {
  top: -12px;
  left: 34px;
  font-size: 12px;
  color: #4a90e2;
  background: #ffffff;
  padding: 0 4px;
}

/* Îçî ÏòàÏÅú transition */
.Input_Group input:not(:placeholder-shown) {
  background: #ffffff;
}

/* Î™®Î∞îÏùº ÎåÄÏùë */
@media (max-width: 480px) {
  .Input_Group input {
    height: 44px;
    font-size: 14px;
  }

  .Input_Group label {
    font-size: 14px;
  }

  .Input_Group input:focus + label,
  .Input_Group input:not(:placeholder-shown) + label {
    font-size: 11px;
  }
}
