/* ================================================
   ‚ú® SIGNUP CARD ‚Äì Modern 2025 Hongilin Ïä§ÌÉÄÏùº
================================================ */

.signup-container {
  width: 400px;
  margin: 50px auto;
  padding: 32px;
  border-radius: 18px;
  background: #ffffff;
  box-shadow: 0 8px 24px rgba(0,0,0,0.08);
  position: relative;
  font-family: 'Pretendard', sans-serif;
  transition: 0.3s ease;
}

.signup-container h2 {
  text-align: center;
  margin-bottom: 24px;
  font-size: 1.6rem;
  font-weight: 700;
  color: #051D42;
}

/* ================================================
   STEP Ïï†ÎãàÎ©îÏù¥ÏÖò
================================================ */

.step {
  opacity: 0;
  transform: translateY(12px);
  transition: all 0.35s ease;
  margin-bottom: 1.2rem;
}

.step.active {
  opacity: 1;
  transform: translateY(0);
}

/* ================================================
   ‚ú® INPUT (ÌîåÎ°úÌåÖ ÎùºÎ≤® + ÏïÑÏù¥ÏΩò + Ïπ¥Îìú Ïä§ÌÉÄÏùº)
================================================ */

.Input_Group_S {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.Input_Group_S input {
  width: 100%;
  height: 48px;
  padding: 14px 14px 14px 44px;
  border: 1.5px solid #d8dbe4;
  border-radius: 12px;
  background: #f7f9ff;
  font-size: 15px;
  transition: all 0.25s ease;
  outline: none;
}

.Input_Group_S input:focus {
  border-color: #4a90e2;
  background: #fff;
  box-shadow: 0 0 7px rgba(74,144,226,0.28);
}

.Input_Group_S i {
  position: absolute;
  left: 14px;
  top: 40%;
  transform: translateY(-46%);
  font-size: 24px;
  color: #8f96a7;
  transition: 0.2s ease;
}

.Input_Group_S input:focus ~ i {
  color: #4a90e2;
}

/* Floating Label */
.Input_Group_S label {
  position: absolute;
  left: 44px;
  top: 40%;
  transform: translateY(-46%);
  background: transparent;
  font-size: 20px;
  color: #9ea4b3;
  padding: 0 6px;
  pointer-events: none;
  transition: all 0.25s ease;
}

.Input_Group_S input:focus + label,
.Input_Group_S input:not(:placeholder-shown) + label {
  top: -6px;
  left: 40px;
  font-size: 12px;
  background: #fff;
  color: #4a90e2;
  font-weight: 600;
}

/* ================================================
   ÌååÏùº ÏóÖÎ°úÎìú (Îçî ÏòàÏÅú Î∞î ÎîîÏûêÏù∏)
================================================ */

.file-upload-wrapper {
  width: 100%;
  margin: 1.4rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeInUp 0.4s forwards;
}

.file-upload-label {
  padding: 14px 26px;
  border-radius: 12px;
  background: #051D42;
  color: #fff;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 700;
  transition: 0.3s ease;
}

.file-upload-label:hover {
  transform: scale(1.05);
}

.file-upload-filename {
  margin-top: 12px;
  font-size: 0.9rem;
  color: #444;
}

/* ================================================
   Ï≤¥ÌÅ¨Î∞ïÏä§ (Îçî Î∂ÄÎìúÎü¨Ïö¥ UI)
================================================ */

.terms {
  display: flex;
  align-items: center;
  gap: 6px;
  margin: 12px 0;
  font-size: 0.92rem;
  color: #444;
}

.terms input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: #4a90e2;
}

.terms a {
  color: #4a90e2;
  text-decoration: underline;
}

/* ================================================
   Ï†úÏ∂ú Î≤ÑÌäº
================================================ */

.signup-container button[type="submit"] {
  width: 100%;
  height: 48px;
  border: none;
  border-radius: 12px;
  background: #051D42;
  color: white;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.25s ease;
}

.signup-container button[type="submit"]:hover {
  transform: scale(1.02);
  background: #072a6b;
}

/* ================================================
   SNS Î°úÍ∑∏Ïù∏
================================================ */

.social-wrapper {
  margin-top: 2rem;
  text-align: center;
  animation: fadeInUp 0.5s forwards 0.3s;
}

.social-text {
  font-size: 0.92rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #555;
}

.social-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-buttons button {
  width: 46px;
  height: 46px;
  border-radius: 46%;
  border: none;
  box-shadow: 0 3px 6px rgba(0,0,0,0.1);
  cursor: pointer;
  transition: transform 0.25s;
}

.social-buttons button:hover {
  transform: scale(1.12);
}

.google-button {
  background: #fff;
  border: 1px solid #ddd;
}

.google-button img {
  width: 26px;
}

.naver-button {
  background: #03c75a;
  border: 1px solid #03c75a;
}

.naver-button img {
  width: 28px;
}

/* ================================================
   Keyframes
================================================ */
@keyframes fadeInUp {
  0% { opacity: 0; transform: translateY(10px); }
  100% { opacity: 1; transform: translateY(0); }
}

/* ================================================
   Î™®Î∞îÏùº ÏµúÏ†ÅÌôî
================================================ */

@media (max-width: 480px) {
  .signup-container {
    width: 92%;
    padding: 22px;
    margin: 30px auto;
    border-radius: 14px;
  }

  .Input_Group_S input {
    height: 44px;
    font-size: 14px;
  }

  .Input_Group_S label {
    font-size: 14px;
  }

  .Input_Group_S input:focus + label,
  .Input_Group_S input:not(:placeholder-shown) + label {
    font-size: 11px;
  }

  .signup-container button[type="submit"] {
    height: 46px;
    font-size: 0.95rem;
  }
}
